body{background-color:#B3D6DA;}
.topicon{
    height:60px;
    width:180px;
}
.img{
    height: 400px;
width: 400px;
}
.img1,.img2,.img3  {
    height: 300px;
width: 300px;}
.topnav li a{
    text-decoration:none;
    font-size:16px;
    color:white;
}
.alllist{
    list-style: none;
    width:100%;
    height:100%;
    justify-content: space-around;
    margin-left:auto;
    align-items:center;
    display:flex;
    flex-direction:column;
}

.alllist li a {
    color: blueviolet;
    text-decoration:none;
    font-size:16px;
}

.line{
    width: 30px;
    height:3px;
    background: white;
    margin: 5px;
    }
  
    .hamburger{
        position: absolute;
        cursor: pointer;
        left: 5%;
        top: 50%;
        transform: translate(-5%,-50%);
        
    }

    .sidebar.close{
        left:0;
        animation: slide-closed 1s forwards
       
    }


    @keyframes slide-open{
    100% {left:0}
    }
    @keyframes slide-closed{
    100% {left:-100%}
        }
    